.img-100 {
  width: 100%;
  height: auto;
}

.td-200 {
  transition-duration: 0.2s;
}
body {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  width: 100vw;
  background: linear-gradient(45deg, var(--themeLight-gradient));
  color: #fff;
}

.app {
  width: 400px;
  min-height: 200px;
  height: min-content;
  border-radius: 32px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  // background-color: #fff9;
  backdrop-filter: blur(10px);
}

.score {
  &-section {
    display: flex;
    font-size: 24px;
    align-items: center;
    width: 100%;
    flex-direction: column;
  }

  &-description {
    font-size: 20px;
    line-height: 150%;
  }
}

/* QUESTION/TIMER/LEFT SECTION */
.question {
  &-section {
    width: 100%;
    position: relative;
    text-align: center;
  }
  &-count {
    margin-bottom: 20px;

    & span {
      font-size: 28px;
    }
  }
  &-video {
    margin-bottom: 0.5rem;
    padding: 0;
    border-radius: 10px;
    margin-bottom: 8px;
    width: 100%;
    height: 100%;
  }

  &-text {
    padding-bottom: 1rem;
  }
}

/* ANSWERS/RIGHT SECTION */
.answer {
  &-section {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &-description {
    font-size: 16px;
    margin-bottom: 16px;
    line-height: 150%;
    padding: 0 8px;
  }

  &-text {
    margin: 8px 0 16px 0;
    padding: 0 8px;
    font-size: 18px;
  }
}

video {
  border-radius: 10px;
  margin-bottom: 8px;
  max-width: 100%;
}

button {
  cursor: pointer;
  &:focus {
    outline: none;
  }

  &:hover {
    background-color: transparent;
  }

  &.btn-next {
    padding: 12px 8px;
    border-radius: 32px;
    background-color: var(--color-link);
    width: 100%;
  }
}

.btn {
  cursor: pointer;
  border: 0px solid #0000;
  font-size: 20px;
  transition-duration: 0.3s;

  &-link{
    background-color: transparent;
    color: #FFF;
    padding: 20px;;
  }

  &-answer {
    width: 100%;
    padding: 16px 8px;
    border-radius: 32px;
    font-size: 16px;
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    border: 1px solid var(--color-light-1);
    color: #eee;
    background-color: transparent;
    margin-bottom: 8px;
    transition-duration: 0.3s;
    cursor: pointer;
    font-weight: 400;

    &.hidden {
      display: none;
    }

    &.correct {
      background-color: var(--color-correct);
    }

    &.incorrect {
      background-color: var(--color-incorrect);
    }

    &:hover:not(.incorrect):not(.correct) {
      background-color: #fff3;
    }
  }
}

@media (max-width: 600px) {
  .app {
    width: 100vw;
    min-height: 100vh;
    border-radius: 0px;
    padding: 20px 20px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
  }
}

@media (prefers-color-scheme: dark) {
  body {
    background: linear-gradient(45deg, var(--themeDark-gradient));
  }
}
